@use '../base/settings' as settings;
@use 'sass:color';

.journal__main-content {
    display: flex;
}

.journal__sidebar {
    background-color: settings.$dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar {
    display: flex;
    justify-content: space-between;
}

.journal__sidebar-navbar h3 {
    font-weight: lighter;   
}

.journal__new-entry {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    transition: color .3s ease-in-out;

    &:hover {
        color: color.adjust(white, $lightness: -18%);
    }
}

.journal__entries {
    flex: 1 1 auto;
    margin-top: 30px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        display: none;
    }
}

.journal__entry {
    background-color: white;
    border-radius: 4px;
    color: settings.$dark-grey;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    overflow: hidden;
}

.journal_entry-picture {
    height: 75px;
    width: 170px;
}

.journal_entry-body {
    padding: 10px;
}
.journal__entry-title {
    font-size: 14px;
    font-weight: bold;
}
.journal__entry-content {
    font-size: 10px;
}

.journal__entry-date-box {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
}

.journal__entry-date-box span {
    font-size: 12px;
}